<template>
  <div id="main-view">
    <h2>Hello, This is Test Page</h2>
    <p>emailaddress:<input type="text" v-model="emailaddress" /></p>
    <p>password:<input type="password" v-model="password" /></p>
    <p class="login-button-area">
      <button @click="login">Login!</button>
      <transition name="slide-fade"
        ><svg
          aria-hidden="true"
          height="24"
          viewBox="0 0 24 24"
          version="1.1"
          width="24"
          data-view-component="true"
          class="octicon octicon-lock blankslate-icon"
          v-if="show"
        >
          <path
            fill-rule="evenodd"
            d="M6 9V7.25C6 3.845 8.503 1 12 1s6 2.845 6 6.25V9h.5a2.5 2.5 0 012.5 2.5v8a2.5 2.5 0 01-2.5 2.5h-13A2.5 2.5 0 013 19.5v-8A2.5 2.5 0 015.5 9H6zm1.5-1.75C7.5 4.58 9.422 2.5 12 2.5c2.578 0 4.5 2.08 4.5 4.75V9h-9V7.25zm-3 4.25a1 1 0 011-1h13a1 1 0 011 1v8a1 1 0 01-1 1h-13a1 1 0 01-1-1v-8z"
          ></path>
        </svg>
      </transition>
    </p>
    <button @click="logout">Logout</button><br />
    <input type="text" v-model="searchWord" />
    <button @click="search">Search</button>
    <table border="1">
      <tbody>
        <tr>
          <th>商品名</th>
          <th>価格</th>
          <th v-if="isLogin">購入</th>
        </tr>
        <tr v-for="item in itemList" :key="item.itemId">
          <td>{{ item.itemName }}</td>
          <td>{{ item.itemPrice }}</td>
          <td v-if="isLogin"><button @click="buy(item)">購入する</button></td>
        </tr>
      </tbody>
    </table>
    <p v-if="isLogin">購入リスト</p>
    <table border="1" v-if="isLogin">
      <tbody>
        <tr>
          <th>商品名</th>
          <th>価格</th>
          <th>削除</th>
        </tr>
        <tr v-for="item in buyItemList" :key="item.itemId">
          <td>{{ item.name }}</td>
          <td>{{ item.price }}</td>
          <td><button @click="deleteItem(item)">削除</button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      emailaddress: "",
      password: "",
      searchWord: "",
      token: "",
      show: true,
      isLogin: false,
      itemList: [],
      buyItemList: [],
    };
  },
  methods: {
    login: function () {
      const url = location.origin + "/";
      const params = new URLSearchParams();
      params.append("emailaddress", this.emailaddress);
      params.append("password", this.password);
      this.axios
        .post(url, params, this.serverPass + "login")
        .then((response) => {
          console.log(response);
          this.token = response.headers.authorization;
          this.show = false;
          this.isLogin = true;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    logout: function () {
      this.show = true;
      this.isLogin = false;
      const url = location.origin + "/api/v1/deleteTokenIdForLogOut";

      const params = new URLSearchParams();
      console.log(this.token);
      this.axios
        .post(
          url,
          params,
          {
            headers: {
              Authorization: this.token,
            },
          },
          this.serverPass + "login"
        )
        .then((response) => {
          console.log(response);
          this.itemList = [];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    search: function () {
      if (this.searchWord == "" || this.searchWord == null) {
        return;
      }
      console.log(this.itemList);
      const url = location.origin + "/api/v1/getResultBySearchWord";
      const params = new URLSearchParams();
      params.append("searchWord", this.searchWord);
      this.axios
        .post(url, params, this.serverPass + "login")
        .then((response) => {
          console.log(response);
          if (
            response.data.indexOf(
              '<!doctype html><html lang=""><head><meta charset="utf-8">'
            ) !== -1
          ) {
            return;
          }
          this.itemList = response.data;
        })
        .catch((error) => {
          console.log(this.itemList);
          console.log(error);
        });
    },
    buy: function (item) {
      console.log(item);
      this.buyItemList.push({
        id: item.itemId,
        name: item.itemName,
        price: item.itemPrice,
      });
    },
    deleteItem: function (item) {
      this.buyItemList = this.buyItemList.filter(
        (buyItem) => buyItem.id !== item.id
      );
    },
  },
};
</script>
